//@ts-nocheck
import type { Chart } from "chart.js";
import { WageredArrays } from "../../types";

export const setChartSettings = (
  data: WageredArrays,
): Chart.ChartConfiguration => {
  return {
    type: "line",
    data: {
      labels: data.labels,
      datasets: [
        {
          data: data.values,
          label: "",
          pointBorderColor: "transparent",
          pointHoverBackgroundColor: "#FFFFFF",
          pointHoverBorderColor: "rgba(255, 255, 255, 0.3)",
          pointHoverBorderWidth: 15,
          pointBorderWidth: 10,
          borderWidth: 4,

          backgroundColor: "transparent",
          cubicInterpolationMode: "monotone",
          borderColor: (context: any) => {
            const { ctx, chartArea } = context.chart.chart;
            if (!chartArea) {
              return null;
            }
            let gradient;
            gradient = ctx.createLinearGradient(
              chartArea.left,
              0,
              chartArea.right,
              0,
            );
            gradient.addColorStop(0, "#e44d88");
            gradient.addColorStop(0.5, "#e930ff");
            gradient.addColorStop(1, "#ff8944");
            return gradient;
          },
        },
      ],
    },
    options: {
      responsive: true,
      legend: {
        display: false,
      },
      tooltips: {
        intersect: false,
        mode: "index",
        axis: "x",
        enabled: false,
        custom: function (tooltipModel) {
          const tooltip = document.getElementById("tooltip");
          const tooltipTop = document.getElementById("tooltip-top");
          const tooltipBottom = document.getElementById("tooltip-bottom");
          const tooltipLabel = document.getElementById("tooltip-label");

          if (!tooltip) {
            return;
          }
          if (tooltipModel.opacity === 0) {
            tooltip.style.opacity = "0";
            return;
          }
          tooltip.style.opacity = "1";
          const chart = this._chart;
          const position = chart.canvas.getBoundingClientRect();
          tooltip.style.height =
            position.height -
            tooltipModel.footerFontSize -
            tooltipModel.footerMarginTop +
            "px";
          const { offsetLeft: positionX, offsetTop: positionY } = chart.canvas;
          tooltip.style.top = positionY + "px";
          tooltip.style.left =
            positionX + tooltipModel.caretX - tooltip.clientWidth / 2 + "px";
          tooltipBottom.style.top = tooltipModel.caretY + 30 + "px";
          let top;
          if (tooltipModel.caretY - 30 - tooltipLabel.clientHeight < 0) {
            top = tooltipModel.caretY + 35 + "px";
            tooltipTop.style.height = "0px";
          } else {
            top = "5px";
            tooltipTop.style.height = tooltipModel.caretY - 25 + "px";
          }
          tooltipLabel.style.top = top;
          const elems = tooltipLabel.getElementsByTagName("span");
          elems[0].innerHTML = tooltipModel.dataPoints[0].label;
          elems[1].innerHTML = `$ ${Number(
            tooltipModel.dataPoints[0].value,
          ).toLocaleString("en-US", {
            maximumFractionDigits: 2,
          })} $`;
        },
      },
      hover: {
        intersect: false,
      },
      scales: {
        xAxes: [
          {
            ticks: {
              fontFamily: "Gilroy",
              fontColor: "rgba(255, 255, 255, 0.5)",
              fontSize: 12,
              maxRotation: 0,
              minRotation: 0,
              autoSkip: true,
              maxTicksLimit: 4,
              padding: 10,
            },
            gridLines: {
              display: false,
            },
          },
        ],
        yAxes: [
          {
            ticks: {
              fontFamily: "Gilroy",
              fontColor: "rgba(255, 255, 255, 0.5)",
              fontSize: 12,
            },
            gridLines: {
              display: false,
            },
          },
        ],
      },
    },
  };
};
