import React, { useEffect, useState } from "react";
import css from "./style.module.scss";
import { useGetLocale } from "providers";
import { SelectCoin } from "./SelectCoin";
import { Coin, FetchPaymentHistory, PayoutData } from "../../types";
import { useFetch, useLocalStorage } from "hooks";
import { Table } from "components";

interface Props {
  coins: Coin[];
}

export const HistoryTable = ({ coins }: Props) => {
  const intl = useGetLocale();
  const [token] = useLocalStorage("token");
  const [fetch, { called, loading, data, error }] =
    useFetch<FetchPaymentHistory>("houseedgesharing/get-payouts");
  const [selectedCoin, setSelectedCoin] = useState<string>("BTC");
  const [history, setHistory] = useState<PayoutData[]>([]);

  useEffect(() => {
    if (!token || !coins.length) {
      return;
    }
    fetch({
      variables: {
        token: token,
        coin: selectedCoin,
      },
    });
  }, [fetch, token, selectedCoin, coins]);

  useEffect(() => {
    if (!called || loading || error || !data) {
      return;
    }

    const arr: PayoutData[] = [];
    const selected: Coin | undefined = coins.find(
      (item) => item.coin === selectedCoin,
    );
    if (!selected) {
      return;
    }
    for (let i = 0; i < data.payouts.length; i++) {
      arr.push({
        ...data.payouts[i],
        ...selected,
      });
    }

    setHistory(arr);
  }, [called, loading, data, error]);

  const onCoinChange = (val: string) => {
    setSelectedCoin(val);
  };

  if (!token) {
    return null;
  }

  return (
    <div className={css.root}>
      <div className={css.header}>
        <div className={css.tableTitle}>
          {intl.formatMessage({
            id: "feyorraHouseEdge.historyTable.payoutHistory",
          })}
        </div>
        <SelectCoin onValueChange={onCoinChange} options={coins} />
      </div>
      <Table
        root={{
          className: css.tableContainer,
        }}
        head={{
          values: [
            intl.formatMessage({
              id: "feyorraHouseEdge.payoutsTable.coin",
            }),
            intl.formatMessage({
              id: "feyorraHouseEdge.historyTable.date",
            }),
            intl.formatMessage({
              id: "feyorraHouseEdge.historyTable.share",
            }),
            intl.formatMessage({
              id: "feyorraHouseEdge.historyTable.totalPayout",
            }),
            intl.formatMessage({
              id: "feyorraHouseEdge.historyTable.bonus",
            }),
          ],
          classes: {
            th: css.tHeaderCell,
            thead: css.tHead,
          },
        }}
        body={{
          rows: history.map((historyItem, index) => {
            return {
              values: [
                <div className={css.tCoinWrapper}>
                  <img
                    src={`https://cdn.pasino.com/coins/${historyItem.coin.toLowerCase()}.png`}
                    alt={historyItem.coin}
                  />
                  <span>{historyItem.name}</span>
                </div>,
                <div>{historyItem.date}</div>,
                <div>{historyItem.share}</div>,
                <div>
                  {historyItem.amount_received} {historyItem.coin}
                </div>,
                <div>
                  {historyItem.bonus_amount} {historyItem.coin}
                </div>,
              ],
              key: `${historyItem.coin}_${index}_history`,
            };
          }),
          classes: {
            td: css.tCell,
          },
        }}
      />
    </div>
  );
};
