import React, { useState } from "react";

import Countdown from "react-countdown";

import { Renderer } from "./renderer";
import css from "./style.module.scss";

export const Counter = () => {
  const [counterDate, setCounterDate] = useState(
    new Date().setUTCHours(24, 0, 0, 0) + 300000,
  );

  const onTimerFinish = () => {
    setCounterDate(new Date().setUTCHours(24, 0, 0, 0) + 300000);
  };

  return (
    <div className={css.timerBox}>
      <p className={css.timerTitle}>Distribution In</p>
      <Countdown
        overtime={true}
        date={counterDate}
        className={css.timer}
        renderer={Renderer}
        onComplete={onTimerFinish}
      />
    </div>
  );
};
