import React from "react";
import css from "./style.module.scss";

export const Tooltip = () => {
  return (
    <div id="tooltip" className={css.tooltip}>
      <div className={css.line} />
      <div id="tooltip-top" className={css.tooltipTop} />
      <div id="tooltip-bottom" className={css.tooltipBottom} />
      <div id="tooltip-label" className={css.tooltipLabel}>
        <span></span>
        <span></span>
      </div>
    </div>
  );
};
