//@ts-nocheck
import React, { useEffect, useRef } from "react";
import { chartJS } from "utils/ChartJS";
import type { Chart } from "chart.js";

import { Tooltip } from "./Tooltip";
import { setChartSettings } from "./chartSettings";
import { WageredArrays } from "../../types";
import { useGetLocale } from "providers";
import css from "./style.module.scss";

interface Props {
  wageredData: WageredArrays;
}

export const WageringGraph = ({ wageredData }: Props) => {
  const canvasRef = useRef<HTMLCanvasElement | null>(null);
  const classRef = useRef<Chart>();
  const intl = useGetLocale();

  useEffect(() => {
    if (
      canvasRef.current &&
      !classRef.current &&
      wageredData.values.length &&
      wageredData.labels.length
    ) {
      chartJS.get((Chart) => {
        let draw = Chart.controllers.line.prototype.draw;
        Chart.controllers.line = Chart.controllers.line.extend({
          draw: function () {
            draw.apply(this, arguments);
            let ctx = this.chart.chart.ctx;
            let _stroke = ctx.stroke;
            ctx.stroke = function () {
              ctx.save();
              ctx.shadowColor = "#E56590";
              ctx.shadowBlur = 10;

              _stroke.apply(this, arguments);
              ctx.restore();
            };
          },
        });

        classRef.current = new Chart(
          canvasRef.current,
          setChartSettings(wageredData),
        );
      });
    }
    return () => {
      classRef.current?.destroy();
    };
  }, [canvasRef]);

  return (
    <div>
      <div className={css.header}>
        {intl.formatMessage({
          id: "feyorraHouseEdge.data.dailyWageringGraph",
        })}
      </div>
      <canvas ref={canvasRef} className={css.graph} />
      <Tooltip />
    </div>
  );
};
