import React, { useState, useEffect } from "react";
import css from "./style.module.scss";
import { useGetLocale } from "providers";
import { Button, Table } from "components";
import { CoinPayment } from "../../types";

interface Props {
  payments: CoinPayment[];
}

export const PayoutsTable = ({ payments }: Props) => {
  const intl = useGetLocale();

  const [showAll, setShowAll] = useState(false);
  const [arrayToShow, setArrayToShow] = useState<any[]>([]);

  useEffect(() => {
    if (showAll) {
      setArrayToShow(payments);
    } else {
      setArrayToShow(payments.slice(0, 4));
    }
  }, [payments, showAll]);

  return (
    <div>
      <div className={css.tableTitle}>
        {intl.formatMessage({
          id: "feyorraHouseEdge.payoutsTable.payouts",
        })}
      </div>

      <Table
        head={{
          values: [
            intl.formatMessage({
              id: "feyorraHouseEdge.payoutsTable.coin",
            }),
            intl.formatMessage({
              id: "feyorraHouseEdge.payoutsTable.payoutToday",
            }),
            intl.formatMessage({
              id: "feyorraHouseEdge.payoutsTable.earnedTokens",
            }),
          ],
          classes: {
            thead: css.tHead,
            th: css.tHeaderCell,
          },
        }}
        body={{
          rows: arrayToShow.map((payout, index) => {
            return {
              values: [
                <div className={css.tCoinWrapper}>
                  <img
                    src={`https://cdn.pasino.com/coins/${payout.coin.toLowerCase()}.png`}
                    alt={payout.coin}
                  />
                  <span>{payout.name}</span>
                </div>,
                <span>
                  {payout.estimated_earnings_today} {payout.coin}
                </span>,
                <span>
                  {payout.total_wagered_today} {payout.coin}
                </span>,
              ],
              key: `${payout.coin}_${index}_payout`,
            };
          }),
          classes: {
            td: css.tCell,
          },
        }}
        root={{
          className: css.tableContainer,
        }}
      />
      <div className={css.note}>
        <span>{intl.formatMessage({ id: "feyorraHouseEdge.data.note" })}</span>
      </div>
      <div className={css.buttonWrapper}>
        <Button className={css.button} onClick={() => setShowAll(!showAll)}>
          {!showAll
            ? intl.formatMessage({
                id: "feyorraHouseEdge.historyTable.showAll",
              })
            : intl.formatMessage({
                id: "feyorraHouseEdge.historyTable.showLess",
              })}
        </Button>
      </div>
    </div>
  );
};
