import React, { useState } from "react";
import checked from "./checked.png";
import { ReactComponent as ArrowIcon } from "assets/icons/arrow-white.svg";
import { ClickAwayListener } from "components";
import css from "./style.module.scss";
import { useGetLocale } from "providers";

interface Props {
  options: any[];
  onValueChange: (val: string) => void;
}

export const SelectCoin = ({ options, onValueChange }: Props) => {
  const intl = useGetLocale();
  const [value, setValue] = useState<string>("BTC");
  const [showOptionList, setShowOptionList] = useState<boolean>(false);

  const handleListDisplay = () => {
    setShowOptionList(!showOptionList);
  };

  const handleValueChange = (val: string) => {
    onValueChange(val);
    setValue(val);
    setShowOptionList(false);
  };

  const onAwayClick = () => {
    setShowOptionList(false);
  };

  return (
    <ClickAwayListener onClickAway={onAwayClick}>
      <div className={css.root}>
        <div className={css.selectBox} onClick={handleListDisplay}>
          <span>
            {intl.formatMessage({
              id: "feyorraHouseEdge.historyTable.coinType",
            })}
            :
          </span>
          <img
            className={css.coinImg}
            src={`https://cdn.pasino.com/coins/${value.toLowerCase()}.png`}
          />
          <ArrowIcon className={showOptionList ? css.arrow : css.arrowRotate} />
        </div>
        {showOptionList && (
          <div className={css.optionsList}>
            {options?.map((option) => {
              return (
                <div
                  key={option.coin}
                  className={css.optionItem}
                  onClick={() => handleValueChange(option.coin)}>
                  <div
                    className={`${css.checkbox} ${
                      option.coin === value ? `${css.checkboxChecked}` : ""
                    }`}>
                    <img src={checked} />
                  </div>
                  <img
                    className={css.coinImg}
                    src={`https://cdn.pasino.com/coins/${option.coin.toLowerCase()}.png`}
                  />

                  <span>{option.name}</span>
                </div>
              );
            })}
          </div>
        )}
      </div>
    </ClickAwayListener>
  );
};
