import React from "react";
import { DataBox } from "../";
import foxIcon from "assets/icons/meta-mask-fox.svg";
import { useGetLocale } from "providers";
import { UserStakeInfo } from "../../types";
import css from "./style.module.scss";
import { CircularProgress } from "components";

interface Props {
  stakeInfo: UserStakeInfo | null;
  onConnect: () => void;
  onDisconnect: () => void;
  showLoader: boolean;
  isLogged: boolean;
}

export const MetaMaskBox = ({
  stakeInfo,
  onConnect,
  onDisconnect,
  showLoader,
  isLogged,
}: Props) => {
  const intl = useGetLocale();

  if (stakeInfo === null) {
    return null;
  }

  const onMetaMaskClickHandler = () => {
    if (stakeInfo.address) {
      onDisconnect();
    } else {
      onConnect();
    }
  };

  return (
    <div className={css.metaMaskContainer}>
      <div className={css.dataWrapper}>
        <button
          disabled={showLoader}
          className={css.metaMaskButton}
          onClick={onMetaMaskClickHandler}>
          {!showLoader ? (
            <img src={foxIcon} />
          ) : (
            <CircularProgress color="inherit" className={css.loader} />
          )}
          <div className={css.addressWrapper}>
            <span>
              {stakeInfo.has_staked
                ? intl.formatMessage({
                    id: "feyorraHouseEdge.data.unlinkAddress",
                  })
                : intl.formatMessage({
                    id: "feyorraHouseEdge.data.connectMetaMask",
                  })}
            </span>
            {stakeInfo.has_staked && (
              <span className={css.address}>{stakeInfo.address}</span>
            )}
          </div>
        </button>

        <div className={css.stakeWrapper}>
          <span>
            {intl.formatMessage({
              id: "feyorraHouseEdge.data.stake1",
            })}
          </span>
          <span>
            {intl.formatMessage({
              id: "feyorraHouseEdge.data.stake2",
            })}
          </span>
          <span>
            {intl.formatMessage({
              id: "feyorraHouseEdge.data.stake3",
            })}
          </span>
        </div>
        {isLogged && (
          <DataBox
            title={intl.formatMessage({
              id: "feyorraHouseEdge.data.yourShare",
            })}
            text={stakeInfo.address ? stakeInfo.share + "%" : "0.000000%"}
          />
        )}
        {isLogged && (
          <DataBox
            title={intl.formatMessage({
              id: "feyorraHouseEdge.data.feyOnPasino",
            })}
            text={stakeInfo.address ? stakeInfo.staked_amount! : "0"}
            coin={"FEY"}
          />
        )}
        {stakeInfo.total_feyorra_verified !== null && (
          <DataBox
            title={intl.formatMessage({
              id: "feyorraHouseEdge.data.totalFeyOnPasino",
            })}
            text={stakeInfo.total_feyorra_verified!}
            coin={"FEY"}
          />
        )}
      </div>
    </div>
  );
};
