import React from "react";
import { CountdownRenderProps } from "react-countdown";

export const Renderer = ({
  hours,
  minutes,
  seconds,
  props,
}: CountdownRenderProps) => {
  return (
    <div className={props.className}>
      <div>
        {hours}
        <span>HOURS</span>
      </div>
      <div>
        {minutes}
        <span>MIN</span>
      </div>
      <div>
        {seconds}
        <span>SEC</span>
      </div>
    </div>
  );
};
