import React, { useState, useEffect } from "react";
import { useHistory, useLocation } from "react-router-dom";

import { notifyBarManager } from "components";
import { useGetLocale } from "providers";
import { useFetch, useLocalStorage } from "hooks";

import {
  FetchInfoData,
  Coin,
  CoinPayment,
  WageredArrays,
  UserStakeInfo,
  ChallengeFetch,
} from "./types";
import {
  PayoutsTable,
  HistoryTable,
  MetaMaskBox,
  WageringGraph,
  Counter,
} from "./components";
import css from "./style.module.scss";
import feyorraLogo from "assets/icons/feyorra-coin-icon.png";
import { Helmet } from "react-helmet";
import { track } from "utils";

interface StateData {
  coinsList: Coin[];
  paymentsList: CoinPayment[];
  wageredData: WageredArrays | null;
  stakeInfo: UserStakeInfo | null;
}

const FeyorraHouseEdge = () => {
  const intl = useGetLocale();
  const location = useLocation();
  const history = useHistory();
  const params = new URLSearchParams(location.search);
  const [token] = useLocalStorage("token");
  const [fetchInfo, { called, loading, data, error }] = useFetch<FetchInfoData>(
    "houseedgesharing/get-info",
  );
  const [challengeFetch] = useFetch<ChallengeFetch>(
    "houseedgesharing/get-challenge",
  );
  const [fetchAuthorize] = useFetch<any>("houseedgesharing/authorize");
  const [fetchDeauthorize] = useFetch<any>("houseedgesharing/deauthorize");
  const [isConnectInProcess, setIsConnectInProcess] = useState<boolean>(false);
  const [stateData, setStateData] = useState<StateData>({
    coinsList: [],
    paymentsList: [],
    wageredData: null,
    stakeInfo: null,
  });

  useEffect(() => {
    fetchInfo();
  }, [token]);

  useEffect(() => {
    if (!called || loading || error || !data) {
      return;
    }
    const coins: Coin[] = [];
    const payments: CoinPayment[] = [];
    Object.keys(data.payment_table).forEach((key) => {
      if (key !== "GEM") {
        const coin = {
          coin: key,
          name: data.payment_table[key].name,
        };
        coins.push(coin);
        payments.push({
          ...data.payment_table[key],
          ...coin,
        });
      }
    });
    const labels: string[] = Object.keys(data.daily_wagered_data);
    const values: number[] = [];
    labels.forEach((key) => {
      values.push(Number(data.daily_wagered_data[key]));
    });
    setStateData({
      coinsList: coins,
      paymentsList: payments,
      wageredData: {
        labels: labels,
        values: values,
      },
      stakeInfo: {
        ...data.user_stake_info,
        total_feyorra_verified: data.total_feyorra_verified,
      },
    });
  }, [called, loading, data, error]);

  const handleLogin = () => {
    history.push("/page/login", { background: location });
  };

  const onSignMetaMask = async () => {
    if (!token) {
      handleLogin();
      return;
    }
    setIsConnectInProcess(true);
    //@ts-ignore
    if (typeof window.ethereum === "undefined") {
      finishMetaMaskConnection(
        intl.formatMessage({
          id: "feyorraHouseEdge.errors.metaMaskNotInstall",
        }),
      );
      return;
    }
    try {
      //@ts-ignore
      await window.ethereum.request({
        method: "eth_requestAccounts",
      });
    } catch (err) {
      track()?.errors(err);

      finishMetaMaskConnection();
      return;
    }

    const { data, error } = await challengeFetch({
      variables: {
        token: token,
      },
    });
    if (error || !data) {
      finishMetaMaskConnection(error.message);
      return;
    }

    try {
      const ethers = await import("@ethersproject/providers");
      const { Web3Provider } = ethers;
      const provider = new Web3Provider(window.ethereum);
      const signer = provider.getSigner();
      const address = await signer.getAddress();
      const signature = await signer.signMessage(data.challenge);
      const { data: authorize, error: authErr } = await fetchAuthorize({
        variables: {
          token: token,
          address: address,
          signature: signature,
        },
      });
      if (authErr) {
        finishMetaMaskConnection(authErr.message);
        return;
      }
      fetchInfo();
      finishMetaMaskConnection();
    } catch (err) {
      track()?.errors(err);
      finishMetaMaskConnection();
      return;
    }
  };

  const finishMetaMaskConnection = (message?: string) => {
    setIsConnectInProcess(false);
    if (message) {
      notifyBarManager.error({
        timeOut: 4000,
        message,
      });
    }
  };

  const onUnlinkMetaMask = async () => {
    const { data, error } = await fetchDeauthorize({
      variables: {
        token: token,
      },
    });
    if (error) {
      notifyBarManager.error({
        timeOut: 4000,
        message: error.message,
      });
    }
    fetchInfo();
  };
  return (
    <>
      <Helmet>
        <title>Feyorra House Edge Share</title>
      </Helmet>

      <div className={css.root}>
        <div className={css.headerContainer}>
          <div className={css.headerTitle}>
            <img className={css.feyorraLogo} src={feyorraLogo} />
            <span className={css.header}>
              {intl.formatMessage({
                id: "feyorraHouseEdge.header.title",
              })}
            </span>
          </div>
          <span>
            {intl.formatMessage({
              id: "feyorraHouseEdge.header.text",
            })}
          </span>
        </div>
        <div className={css.container}>
          <MetaMaskBox
            stakeInfo={stateData.stakeInfo}
            onConnect={onSignMetaMask}
            onDisconnect={onUnlinkMetaMask}
            showLoader={isConnectInProcess}
            isLogged={token !== "" && token !== null}
          />
          <Counter />
          <PayoutsTable payments={stateData.paymentsList} />
          <HistoryTable coins={stateData.coinsList} />
          {stateData.wageredData && (
            <WageringGraph wageredData={stateData.wageredData} />
          )}
        </div>
      </div>
    </>
  );
};

export default FeyorraHouseEdge;
